import Vue from 'vue';
import VueRouter from 'vue-router';
import ElementUI from 'element-ui';

import App from './App.vue';
import PackageImage from './pages/PackageImage.vue';
import InfoImage from './pages/InfoImage.vue';
import Names from './pages/Names.vue';
// import Editor from './pages/Editor';
import Home from './pages/Home.vue';
import YmeUI from './components';

import 'element-ui/lib/theme-chalk/index.css';
import './styles/index.scss';

Vue.use(ElementUI, {});
Vue.use(YmeUI);
Vue.use(VueRouter);

const routes = [
	{path: '/', component: Home},
	{path: '/package', component: PackageImage},
	{path: '/info', component: InfoImage},
	{path: '/names', component: Names, meta: {title: 'Names'}},
	// {
	// 	path: '/editor',
	// 	// component: Editor,
	// 	component: () => import(/* webpackChunkName: "editor" */ './pages/Editor'),
	// },
	// {
	// 	path: '/workspace',
	// 	component: () => import(/* webpackChunkName: "workspace" */ './pages/WorkSpace'),
	// },
];

const router = new VueRouter({
	routes,
	mode: 'hash',
});

Vue.config.productionTip = false;

new Vue({
	router,
	render: (h) => h(App),
}).$mount('#app');
