import * as html2image from 'html-to-image';
import {saveAs} from 'file-saver';

export function getStyle(obj = {}) {
	const out = {};
	['color', 'fontSize', 'fontWeight', 'lineHeight'].forEach((key) => {
		if (key in obj) {
			out[key] = obj[key];
			if (key === 'fontSize' && out[key]) {
				out[key] += 'px';
			}
		}
	});
	return out;
}

export function getId() {
	return Math.random().toString(16).substr(2, 6) + Math.random().toString(16).substr(2, 6);
}

export async function wait(time = 1000) {
	return new Promise((r) => {
		setTimeout(() => {
			r();
		}, time);
	});
}

export function sortData(list = []) {
	return list.filter(Boolean).sort((a, b) => {
		let aName = parseInt(a.index.toUpperCase().match(/\d{1,}/));
		let bName = parseInt(b.index.toUpperCase().match(/\d{1,}/));
		if (aName < bName) {
			return -1;
		}
		if (aName > bName) {
			return 1;
		}
		return 0;
	});
}

export async function exportPreview(filename) {
	try {
		const blob = await html2image.toBlob(document.getElementById('preview'), {
			pixelRatio: 1,
			backgroundColor: '#fff',
		});
		saveAs(blob, filename);
	} catch (error) {
		console.log(`导出错误 ${filename}`);
		console.error(error);
		throw error;
	}
}

export async function loadFileEntry(entry) {
	return new Promise((resolve) => {
		if (entry.isFile) {
			entry.file((file) => {
				if (['image/jpeg', 'image/png'].includes(file.type)) {
					let fileName = file.name.trim();
					fileName = fileName.substr(0, fileName.lastIndexOf('.'));

					let index = fileName;
					let name = fileName;

					// like 100_29
					if (/^(\d{1,})_(.*?)$/.test(fileName)) {
						// 100_29 -> [,100,29]
						const [, first, last] = /^(\d{1,})_(.*?)$/.exec(fileName);
						index = first;
						name = last;
					}

					resolve({
						index,
						name,
						file,
					});
				} else if (/\.csv$/.test(file.name)) {
					// 文档类，貌似没有类型，用后缀先判断了
					resolve({
						csv: true,
						file,
						entry,
					});
				} else {
					console.log('文件类型错误', file);
					return resolve(null);
				}
			});
		} else {
			return resolve(null);
		}
	});
}

export async function loadEntries(dir) {
	return new Promise((resolve) => {
		if (dir && dir.isDirectory) {
			const reader = dir.createReader();
			reader.readEntries(async (entries) => {
				let data = await Promise.all(entries.map((entry) => loadFileEntry(entry)));
				data = sortData(data);
				resolve(data);
			});
		}
	});
}

export function flatten(arr, depth = 1) {
	return arr.reduce(
		(a, v) => a.concat(depth > 1 && Array.isArray(v) ? flatten(v, depth - 1) : v),
		[]
	);
}
