<template>
	<div class="text relative" :style="style" @dblclick="onEdit">
		<span class="remove" @click="onRemove">
			<icon-x-circle />
		</span>
		<span class="" v-html="text"></span>
	</div>
</template>

<script>
import {getStyle} from '../utils';

export default {
	name: 'yme-text',
	props: {
		data: Object,
		text: String,
	},
	data() {
		return {};
	},
	computed: {
		style() {
			return getStyle(this.data);
		},
	},
	methods: {
		onRemove() {
			this.$emit('remove', this.data);
		},
		onEdit() {
			this.$emit('dblclick');
		},
	},
};
</script>

<style lang="scss" scoped>
.text {
	display: inline-flex;
	line-height: 1;
	user-select: none;

	&:hover {
		.remove {
			display: inline-flex;
		}
	}
}

.remove {
	cursor: pointer;
	z-index: 10;
	background: white;
	border-radius: 50%;
	overflow: hidden;
	display: none;
	position: absolute;
	right: -7px;
	top: -7px;

	svg {
		width: 14px;
		height: 14px;
	}
}
</style>
