		value: 'clip-polygon',
	},
	{
		label: '嘉鸿3x2',
		value: 'clip-polygon-line',
	},
	{
		label: '菱形◆',
		value: 'clip-diamond',
	},
	{
		label: '吉米300x',
		value: 'clip-jimi',
	},
];

export default {
