<template>
	<div id="dragndrop" class="drag-and-drop" :class="{dragging: dragging}" @dragover.prevent>
		<span class="tip">{{ tip }}</span>
	</div>
</template>

<script>
import {loadFileEntry, sortData, flatten} from '../utils';
import {read} from '../utils/data-transfer';

export default {
	name: 'DragAndDrop',
	props: {
		deep: {
			type: Number,
			default: 2,
		},
		tip: {
			type: String,
			default: '放手导入文件',
		},
	},
	mounted() {
		let elms = new Set();
		window.addEventListener('dragenter', (e) => {
			if (e.dataTransfer.effectAllowed === 'move') return;
			if (elms.size === 0) this.onDragEnter();
			elms.add(e.target);
		});
		window.addEventListener('drop', (e) => {
			elms.delete(e.target);
			this.onFolderDrop(e);
			// this.onDrop(e);
		});
		window.addEventListener('dragleave', (e) => {
			elms.delete(e.target);
			if (elms.size === 0) this.onDragLeave();
		});
	},
	data: function () {
		return {
			dragging: false,
			nextList: [],
		};
	},
	methods: {
		onDragLeave() {
			this.dragging = false;
		},
		onDragEnter() {
			this.dragging = true;
		},
		async onDrop(e) {
			e.preventDefault();
			this.dragging = false;

			const result = await read(e.dataTransfer.items);
			this.$emit('change', result);
		},
		async onFolderDrop(e) {
			e.preventDefault();

			this.dragging = false;
			if (e.dataTransfer.items) {
				// console.log(e.dataTransfer.items);
				let items = Array.from(e.dataTransfer.items);
				let nextList = [];
				// convert to entry -webkit-
				items = items.map((item) => item.webkitGetAsEntry());
				const fileOnly = items.every((item) => item.isFile);
				const folderOnly = items.every((item) => item.isDirectory);

				// 都是文件
				if (fileOnly) {
					nextList = await Promise.all(items.map((item) => loadFileEntry(item)));
					nextList = sortData(nextList);
					this.$emit('add', {data: nextList});
					return;
				}

				// 多个文件夹
				if (folderOnly) {
					const folders = await this.formatFolders(items);
					this.$emit('change', {folders});
					return;
				}

				const firstDir = items.find((item) => item.isDirectory);
				// 当前第一个文件夹
				if (firstDir) {
					let directoryReader = firstDir.createReader();
					// 读取子文件
					directoryReader.readEntries(async (entries) => {
						nextList = await Promise.all(
							entries.map((entry) => loadFileEntry(entry))
						);
						nextList = sortData(nextList);
						console.log(`文件夹有 ${nextList.length} 张图片`);
						this.$message.success(`文件夹有 ${nextList.length} 张图片`);
						this.$emit('drop', {name: firstDir.name, data: nextList});
					});
				}
			}
		},
		async formatFolders(items = []) {
			// [[], [], a, b]
			let folders = await Promise.all(items.map((item) => this.scanFolder(item)));
			folders = flatten(folders);
			return folders;
		},
		async scanFolder(item) {
			return new Promise((resolve) => {
				// TODO 支持多层？
				if (item.isDirectory && this.deep === 2) {
					const reader = item.createReader();
					reader.readEntries(async (entries) => {
						const folders = [];
						entries.forEach((entry) => {
							// 1 | 2 | 3
							if (entry.isDirectory && /\d{1,}/.test(entry.name)) {
								folders.push(entry);
							}
						});
						if (folders.length) {
							resolve(folders);
						} else {
							resolve([item]);
						}
					});
				} else {
					resolve([item]);
				}
			});
		},
	},
};
</script>

<style lang="scss" scoped>
.drag-and-drop {
	background: transparent;
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;

	display: none;
	align-items: center;
	justify-content: center;

	// .tip {}

	&.dragging {
		display: flex;
		z-index: 100;
		background: #fff;
		opacity: 0.8;

		.tip {
			display: block;
		}
	}
}
</style>
