<template>
	<el-popover v-model="visible" trigger="click" popper-class="p-0">
		<div class="flex" @click="onClick">
			<div data-type="grid" class="cursor-pointer p-4 hover:bg-gray-50">网格</div>
			<div data-type="image" class="cursor-pointer p-4 hover:bg-gray-50">图片</div>
			<div data-type="label" class="cursor-pointer p-4 hover:bg-gray-50">文本</div>
		</div>
		<template #reference>
			<slot>
				<el-button type="primary" size="small">添加</el-button>
			</slot>
		</template>
	</el-popover>
</template>

<script>
export default {
	name: 'NodePopover',
	data() {
		return {
			visible: false,
		};
	},
	methods: {
		onClick(e) {
			const {type} = e.target.dataset;
			if (type) {
				this.$emit('add', {type});
				this.visible = false;
			}
		},
	},
};
</script>

