<template>
	<component
		:is="tag"
		class="button"
		@click="onClick"
	>
		<slot></slot>
	</component>
</template>

<script>
export default {
	name: 'YButton',
	props: {
		tag: {
			type: String,
			default: 'button',
		},
	},
	methods: {
		onClick(e) {
			this.$emit('click', e);
		},
	},
};
</script>

<style>
.button {
	@apply px-4 h-14 inline-flex hover:bg-gray-100 items-center justify-center;
	@apply outline-none focus:outline-none;
}
</style>
