import {randomId} from './random';

export function flatten(arr, depth = 1) {
	return arr.reduce(
		(a, v) => a.concat(depth > 1 && Array.isArray(v) ? flatten(v, depth - 1) : v),
		[]
	);
}

async function formatFolders(folders = []) {
	let result = await Promise.all(folders.map((item) => scanFolder(item)));
	result = flatten(result);
	return result;
}

async function scanFolder(folder) {
	return new Promise((resolve) => {
		if (folder.isDirectory) {
			const reader = folder.createReader();
			reader.readEntries(async (entries) => {
				const folders = [];
				entries.forEach((entry) => {
					// 1 | 2 | 3
					if (entry.isDirectory && /\d{1,}/.test(entry.name)) {
						folders.push(entry);
					}
				});
				if (folders.length) {
					resolve(folders);
				} else {
					resolve([folder]);
				}
			});
		} else {
			resolve([folder]);
		}
	});
}

export async function read(items) {
	const list = Array.from(items)
		.map((item) => item.webkitGetAsEntry())
		.filter((item) => !item.name.startsWith('.'));

	// const fileOnly = list.every((item) => item.isFile);
	const folderOnly = list.every((item) => item.isDirectory);

	if (folderOnly) {
		const folders = await formatFolders(list);
		return {type: 'folder', data: folders};
	}

	// ignore folder
	const fileItems = list.filter((item) => item.isFile);
	const files = await Promise.all(fileItems.map((item) => loadFileEntry(item)));
	return {type: 'file', data: files};
}

export async function loadFileEntry(entry) {
	return new Promise((resolve) => {
		if (entry.isFile) {
			entry.file((file) => {
				if (['image/jpeg', 'image/png'].includes(file.type)) {
					let name = file.name.trim().replace(/\.(png|jpeg)$/, '');
					let index = name;

					if (/^(\d+)_(.*?)$/.test(name)) {
						const [, f, l] = /^(\d+)_(.*?)$/.exec(name) ?? ['', index, name];
						index = f;
						name = l;
					}

					resolve({
						id: randomId(),
						index,
						name,
						file,
					});
				} else if (/\.csv$/.test(file.name)) {
					resolve({
						id: randomId(),
						csv: true,
						file,
						entry,
					});
				} else {
					resolve(null);
				}
			});
		} else {
			resolve(null);
		}
	});
}

export async function loadEntries(dir) {
	return new Promise((resolve) => {
		if (dir && dir.isDirectory) {
			const reader = dir.createReader();
			reader.readEntries(async (entries) => {
				let data = await Promise.all(entries.map((entry) => loadFileEntry(entry)));
				data = data.filter((item) => item !== null);
				// data = sortData(data);
				resolve(data);
			});
		}
	});
}
