<template>
	<el-dialog :visible="show" :destroy-on-close="true" @close="onClose">
		<!-- p -->
		<!-- o -->
		<p :style="labelStyle">{{ setting.label }}</p>
		<div v-if="type === 'text'">
			<el-input v-model="setting.label" />
			<el-color-picker v-model="setting.color" />
			<el-input-number v-model="setting.fontSize" />
			<el-input-number v-model="setting.fontWeight" :step="100" />
		</div>
		<div v-else-if="type === 'image'" class="">
			<!-- w -->
			<el-input v-model="setting.label" />
			<el-input-number v-model="setting.width" />
			<el-input-number v-model="setting.height" />
			<!-- h -->
		</div>
		<span slot="footer" class="dialog-footer">
			<el-button type="primary" @click="onSave">确 定</el-button>
		</span>
	</el-dialog>
</template>

<script>
export default {
	name: 'yme-text-editor',
	props: {},
	computed: {
		labelStyle() {
			return {
				color: this.color,
				fontSize: `${this.fontSize}px`,
				fontWeight: this.fontWeight,
			};
		},
	},
	data() {
		return {
			show: false,
			type: '',
			item: {},
			setting: {},
		};
	},
	methods: {
		edit(textItem) {
			if (textItem === null) {
				this.show = false;
			} else {
				//...
				this.type = textItem.type;
				// this.label = textItem.label;
				// this.fontSize = textItem.fontSize;
				// this.color = textItem.color;
				// this.fontWeight = textItem.fontWeight;
				this.setting = {
					...textItem,
				};
				this.item = {...textItem};
				this.show = true;
			}
		},
		close() {
			this.edit(null);
		},
		onClose() {
			this.close();
		},
		onSave() {
			this.$emit('save', {
				...this.item,
				...this.setting,
				// label: this.label,
				// size: this.size,
				// color: this.color,
				// weight: this.weight,
			});
		},
	},
};
</script>
