<template>
	<div class="block-card has-remove">
		<span class="remove" @click="onClick">
			<icon-x-circle />
		</span>
		<div
			class="block-image-wrapper"
			:style="{width: `${width}px`, height: `${height}px`}"
			:class="{[clipClass]: !!clipClass}"
		>
			<el-image v-loading="loading" fit="cover" class="block-image" :src="image" />
		</div>
		<el-input
			ref="nameInput"
			class="block-name-editor"
			v-if="editName"
			v-model="name"
			@blur="onExitEditName"
		/>
		<span v-else class="block-name" @dblclick="tryEditName">{{ ' ' + name + ' ' }}</span>
	</div>
</template>

<script>
export default {
	name: 'BlockCard',
	props: {
		// image: String,
		// name: String,
		data: {
			type: Object,
			required: true,
		},
		clipClass: String,
		clip: {
			type: String,
			default: 'none',
		},
		width: {
			type: [Number, String],
			default: 100,
		},
		height: {
			type: [Number, String],
			default: 100,
		},
	},
	data: function () {
		return {
			loading: false,
			name: '',
			// 1x1
			image: 'data:image/png;base64,R0lGODlhAQABAAAAACH5BAEKAAEALAAAAAABAAEAAAICTAEAOw==',
			editName: false,
		};
	},
	mounted() {
		this.loadImage();
	},
	methods: {
		onClick() {
			this.$emit('remove', this.data.name);
		},
		loadImage() {
			const {file, name} = this.data;
			this.name = name.toUpperCase().replace(/\.(JPG|PNG)/, '');
			// 如果是图片数据直接使用
			if (typeof file === 'string' && file.startsWith('data:image')) {
				this.image = file;
				return;
				// this.name = name;
			}

			console.log('加载 ', file.name);
			this.loading = true;
			const reader = new FileReader(file);
			reader.onload = (e) => {
				console.log('完成 ', file.name);
				this.loading = false;
				if (
					e.target.result.toString() !==
					'data:image/png;base64,R0lGODlhAQABAAAAACH5BAEKAAEALAAAAAABAAEAAAICTAEAOw=='
				) {
					this.image = e.target.result;
					this.$nextTick(() => {
						this.$emit('done', {name});
					});
				} else {
					this.name = '';
				}
			};
			reader.readAsDataURL(file);
		},
		tryEditName() {
			this.editName = true;
			this.$nextTick(() => {
				this.$refs.nameInput.focus();
			});
		},
		onExitEditName() {
			this.editName = false;
		},
	},
};
</script>

