	position: relative;
	display: inline-flex;
	flex-direction: column;

	&:hover {
		.remove {
			display: inline-flex;
		}
	}
}

.shadow {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
}

.block-name {
	text-align: center;
	font-size: 28px;
	line-height: 1.5;
	height: 1.5em;
	font-weight: 500;
	margin: 0;
	margin-top: 8px;
}
</style>

<style lang="scss">
.block-name-editor {
	.el-input__inner {
		text-align: center;
		font-size: 20px;
	}
}
</style>
