					class="watermark select-none pointer-events-none absolute top-0 left-0 w-full h-full z-10"
					v-if="currentWatermark"
				>
					<!-- currentWatermark -->
					<div class="" :style="watermarkStyle">
						<div class="" v-for="i in rowNumber * 2" :key="i">
							<img class="" :src="currentWatermark" />
						</div>
					</div>
				</div>

				<draggable v-model="list" v-bind="dragOptions" :style="contentStyle">
					<block-card
						v-for="(item, index) in list"
						:key="`${exportFilename}_${item.name}_${index}`"
						:data="item"
						:width="imageWidth"
						:height="imageHeight"
						:clipClass="currentClip"
						@remove="onCardRemove"
						@done="onCardDone"
					/>
				</draggable>
			</div>
			<!--  -->
			<div v-else class="preview-empty text-center">
				<p>没图片</p>
			</div>
