</template>

<script>
import draggable from 'vuedraggable';
import BlockCard from '../components/BlockCard';
import listExport from '../mixins/listExport';
import config from '../config';
import {loadEntries} from '../utils';

export default {
	name: 'PackageImage',
	components: {
		BlockCard,
		draggable,
	},
	mixins: [listExport],
	configList: config,
	data() {
		return {
			configCode: '',
			placeholderName: '',
			// 1x1 png
			placeholderFile:
				'data:image/png;base64,R0lGODlhAQABAAAAACH5BAEKAAEALAAAAAABAAEAAAICTAEAOw==',
			// data.
			list: [],
			listLoaded: 0,

			// ------- settings ----------
			// watermark
			watermark: [],
			currentWatermark: '',
			// row | column
			gridFlow: 'row',
			// item style
			currentClip: 'clip-none',
			// export width
			previewWidth: 1080,
			// 20 -> 60
			previewPadding: '60px 0',
			// column
			columnNumber: 3,
			columnTop: 40,
			// rowNumber: 0,
			// w * h
			imageWidth: 300,
			imageHeight: 200,
			// show/hidden settings
			showDrawer: false,
		};
	},
	computed: {
		dragOptions() {
			return {
				animation: 150,
			};
		},
		contentStyle() {
			// length / columnNumber
			const rows = this.rowNumber;

			return {
				display: 'grid',
				'justify-content': 'center',
				'grid-gap': `${this.columnTop}px`,
				'grid-auto-flow': this.gridFlow,
				'grid-template-columns': `repeat(${this.columnNumber}, ${this.imageWidth}px)`,
				'grid-template-rows': `repeat(${rows}, auto)`,
			};
		},
		watermarkStyle() {
			const height = this.imageHeight + 50 + this.columnTop;
			const width = 175;
			const rows = this.rowNumber;

			return {
				opacity: 0.1,
				'margin-top': `65px`,
				'margin-left': '-44px',
				display: 'grid',
				'grid-template-columns': `repeat(2, ${width}px)`,
				'grid-template-rows': `repeat(${rows}, ${height}px)`,
				'grid-column-gap': `166px`,
				'align-items': 'flex-end',
				'justify-content': 'center',
			};
		},
		rowNumber() {
			let rowNumber = 0;
			if (this.gridFlow === 'column') {
				if (this.list.length <= 5) {
					rowNumber = this.list.length;
				} else {
					rowNumber = Math.max(
						5,
						Math.ceil(this.list.length / this.columnNumber)
					);
				}
			} else {
				rowNumber = Math.ceil(this.list.length / this.columnNumber);
			}
			return rowNumber;
		}
	},
	watch: {
		list: function () {
			this.listLoaded = 0;
		},
		listLoaded: function (loaded) {
			if (loaded > 0 && loaded === this.list.length) {
				// this.switchItemDone();
			}
		},
	},
	methods: {
		clean() {
			this.cleanList();
			this.list = [];
		},
		switchWatermark(file) {
			console.log(file);
			this.currentWatermark = file.url;
		},
		switchConfig(code) {
			if (code in config) {
				const {patch = {}, label} = config[code];
				console.log('切换配置 -> ' + label);
				console.log(patch);
				for (const key in patch) {
					this[key] = patch[key];
				}
			}
		},
		onChangeFolders(e) {
			this.switchListData(e.folders);
		},
		updateList(patch = [], reset = false) {
			const nextList = reset ? [...patch] : [...this.list, ...patch];
			while (nextList.length < 15) {
				nextList.push({
					name: this.placeholderName,
					file: this.placeholderFile,
				});
			}
			this.list = nextList;
		},
		addPlaceholder() {
			this.updateList([
				{
					name: this.placeholderName,
					file: this.placeholderFile,
				},
			]);
		},
		// 添加图片
		onDropAdd(e) {
			console.log(`加 ${e.data.length} 张图片`);
			this.$message.success(`加 ${e.data.length} 张图片`);

			this.updateList(e.data);
		},
		onCardDone() {
			this.listLoaded += 1;
		},
		// 删除图片
		onCardRemove(targetName) {
			const filtered = this.list.filter((item) => item.name !== targetName);
			this.updateList(filtered, true);
		},
		// 换文件夹
		async doSwitchItem(folder) {
			if (folder) {
				const list = await loadEntries(folder);
				const filename = folder.fullPath.substr(1).replace('/', '-');
				// - 切换文件夹数据 -
				this.filename = filename;
				this.updateList(list, true);
				// -
				this.$message.info(`切换当前文件夹 ${folder.fullPath} (${list.length}p)`);
			} else {
				this.list = [];
				this.filename = '';
			}
			return Promise.resolve();
		},
		canExport() {
			return this.list.length > 0;
		},
	},
};
</script>
