<template>
	<div class="page p-4">
		<div class="">
			<el-input v-model:value="input" type="textarea" rows="10" />
		</div>
		<div>
			<div>output</div>
			<el-input :value="output" type="textarea" rows="10"></el-input>
		</div>
	</div>
</template>

<script>
export default {
	data() {
		return {
			input: '',
		};
	},
	computed: {
		output() {
			const str = this.input;
			const arr = str.split(/\n+/).map((item) => {
				return item.split(/\t/).map((i) => i.trim());
			});

			const r = [];

			let c = true;
			while (c) {
				arr.forEach((item) => {
					const n = item.shift();
					n && r.push(n);
				});
				c = arr.some((item) => item.length > 0);
			}

			return r.join('\n');
		},
	},
};
</script>
