<template>
	<div class="page">
		<drag-and-drop :deep="1" @add="onDropAdd" @change="onChangeFolders" />
		<yme-navbar>
			<el-popconfirm title="确定清空所有数据？" @confirm="cleanList">
				<el-button type="danger" slot="reference">清空</el-button>
			</el-popconfirm>

			<el-dropdown
				class="ml-4"
				trigger="click"
				split-button
				@click="handlePlaceCommand('add-image')"
				@command="handlePlaceCommand"
			>
				+图片
				<el-dropdown-menu slot="dropdown">
					<el-dropdown-item command="add-text">文本</el-dropdown-item>
				</el-dropdown-menu>
			</el-dropdown>

			<el-dropdown
				class="ml-4"
				trigger="click"
				split-button
				@click="exportImage"
				@command="onDownloadCommand"
			>
				下载
				<el-dropdown-menu slot="dropdown">
					<el-dropdown-item command="download-all">
						下载全部 ({{ listData.length }})
					</el-dropdown-item>
				</el-dropdown-menu>
			</el-dropdown>

			<el-select class="ml-4 w-32" v-model="currentIndex" placeholder="选择目录">
				<el-option
					v-for="(item, index) in listData"
					:key="item.id"
					:value="index"
					:label="`#${index + 1} ${item.id}`"
				></el-option>
			</el-select>

			<el-select
				class="ml-4 w-24"
				v-model="configCode"
				@change="switchConfig"
				placeholder="配置"
			>
				<el-option
					v-for="c in $options.configList"
					:key="c.code"
					:label="c.label"
					:value="c.code"
				/>
			</el-select>

			<el-button
				class="ml-4"
				v-clipboard:copy="dataString"
				v-clipboard:success="copySuccess"
			>
				拷贝数据
			</el-button>

			<el-switch class="self-center ml-4" v-model="preview"></el-switch>
		</yme-navbar>

		<yme-preview
			:width="previewWidth"
			:tip="`10x10 - width: ${previewWidth}px - height: ${previewHeight}px - name: ${exportFilename}.jpg`"
		>
			<div
				id="preview"
				class="preview relative overflow-hidden"
				:class="{'grid-line': !preview}"
				:style="{height: `${previewHeight}px`}"
			>
				<!-- bg -->
				<div class="background-wrapper absolute left-0 top-0 w-full h-full">
					<el-image fit="cover" :src="backgroundFile" />
				</div>
				<!--  -->
				<vue-draggable-resizable
					:grid="[10, 10]"
					:parent="true"
					w="auto"
					h="auto"
					:resizable="false"
					v-for="item in data"
					:x="item.x"
					:y="item.y"
					:key="`text-${item.id}`"
					:class="{'border-transparent': preview}"
					@dragstop="(x, y) => onTextStop(item.id, x, y)"
				>
					<!--  -->
					<!-- <component :is="`yme-${item.type}`" :data="item"></component> -->
					<yme-text
						v-if="item.type === 'text'"
						:data="item"
						@dblclick="() => onEdit(item)"
						@remove="onItemRemove"
						:text="formatLabel(item)"
					></yme-text>
					<yme-image
						v-else-if="item.type === 'image'"
						:data="item"
						@dblclick="() => onEdit(item)"
						@remove="onItemRemove"
						:file="loadImage(item)"
					></yme-image>
					<!--  -->
				</vue-draggable-resizable>
			</div>
		</yme-preview>
		<!-- dialog editor -->
		<yme-text-editor ref="editor" :data="editorValue" @save="onEditorSave" />
	</div>
</template>

<script>
import VueDraggableResizable from 'vue-draggable-resizable';
import 'vue-draggable-resizable/dist/VueDraggableResizable.css';
import DragAndDrop from '../components/DragAndDrop';
import Text from '../components/Text.vue';
import Image from '../components/Image.vue';
import TextEditor from '../components/TextEditor.vue';
import listExport from '../mixins/listExport';
import clipboard from '../directives/clipboard';
import Papa from 'papaparse';
import config from '../config';
import {getId, loadEntries} from '../utils';

// 行边距
const defaultSpacing = {
	1: 20,
	2: 40,
	3: 60,
};

// const currentConfig = config.jiahong;
// const currentConfig = {info: {}};

export default {
	name: 'InfoImage',
	configList: config,
	components: {
		VueDraggableResizable,
		DragAndDrop,
		[Text.name]: Text,
		[Image.name]: Image,
		[TextEditor.name]: TextEditor,
	},
	directives: {
		clipboard,
	},
	mixins: [listExport],
	data() {
		return {
			configCode: '',
			previewWidth: 1080,
			previewHeight: 300,
			preview: false,
			backgroundFile:
				'data:image/png;base64,R0lGODlhAQABAAAAACH5BAEKAAEALAAAAAABAAEAAAICTAEAOw==',
			placeholdImage:
				'data:image/png;base64,R0lGODlhAQABAAAAACH5BAEKAAEALAAAAAABAAEAAAICTAEAOw==',
			// text
			editorValue: null,
			data: [],
			fileMap: null,
			//
			defaultText: {
				type: 'text',
				label: '文本',
				fontSize: 32,
				color: '#000',
				fontWeight: 400,
			},
			defaultImage: {
				type: 'image',
				width: 100,
				height: 100,
			},
		};
	},
	computed: {
		dataString() {
			return JSON.stringify(this.data);
		},
		spacingList() {
			const spacing = defaultSpacing;

			return Object.keys(spacing).reduce((obj, key) => {
				obj[
					'spacing:' + key
				] = `<span class="spacing-${spacing[key]}" style="height: ${spacing[key]}px"></span>`;
				return obj;
			}, {});
		},
		formatData() {
			return {
				...this.currentItem,
				...this.spacingList,
			};
		},
	},
	watch: {},
	methods: {
		handlePlaceCommand(command) {
			if (command === 'add-text') {
				this.$refs.editor.edit(this.defaultText);
			} else if (command === 'add-image') {
				this.$refs.editor.edit(this.defaultImage);
			}
		},
		//
		copySuccess() {
			this.$message.success('数据拷贝成功');
		},
		//
		onTextStop(id, x = 0, y = 0) {
			const index = this.data.findIndex((item) => item.id === id);
			this.$set(this.data, index, {...this.data[index], x, y});
		},
		onItemRemove(textItem) {
			this.data = this.data.filter((item) => item.id !== textItem.id);
		},
		// 拖资源进来，图片或者数据文件
		onDropAdd(e) {
			// 设置背景图
			const {data = []} = e;
			if (data.length > 0) {
				// use first image
				const {file, csv} = data[0];
				// bg
				console.log(`有个文件`, file);
				if (['image/jpeg', 'image/png'].includes(file.type)) {
					this.switchBackground(file);
				} else if (csv === true) {
					console.log(`应该是个 csv 数据文件`);
					this.switchDataFile(file);
				}
				// data csv
			}
		},
		// 文件夹
		onChangeFolders(e) {
			// a/q.png
			// b/q.png
			const {folders = []} = e;
			// 记录当前
			// this.listData <<-
			let list = [];
			if (this.listData && this.listData.length > 0) {
				list = [...this.listData];
			}
			if (folders.length > 0) {
				for (const folder of folders) {
					const found = list.find((item) => item.id === folder.name);
					if (found) {
						found.folder = folder;
					}
				}

				this.listData = [...list];
			}
			// loadEntries
		},
		// 图片就切换背景图片
		async switchBackground(file) {
			console.log('切换背景图片', file);
			// const entry = await loadFileEntry(file);
			if (file) {
				const reader = new FileReader();
				reader.onload = (e) => {
					this.$message.warning('换了个背景图片');
					this.backgroundFile = e.target.result;
				};
				reader.readAsDataURL(file);
			}
		},
		// 数据文件就切换当前数据
		async switchDataFile(file) {
			// 去掉当前的数据列表
			console.log('切换数据文件', file);
			const reader = new FileReader();
			const loading = this.$loading({
				lock: true,
				text: '切换数据文件...',
			});
			reader.onload = () => {
				const {data} = Papa.parse(reader.result, {header: true, skipEmptyLines: true});
				if (data && data.length > 0) {
					this.switchListData(data.map((item) => ({...item, _id: 'csv-' + getId()})));
					loading.close();
				}
			};
			reader.onerror = () => {
				console.error(reader.error);
				loading.close();
			};
			reader.readAsText(file);
			// 选中第一个
		},
		onEdit(item) {
			this.$refs.editor.edit(item);
		},
		onEditorSave(item) {
			if (item.id !== undefined) {
				const clone = [...this.data];
				const found = clone.find((one) => one.id === item.id);
				if (found) {
					Object.assign(found, item);
				} else {
					this.$message.error('没找到文本资源' + item.id);
					return;
				}
				this.data = clone;
			} else {
				this.data = [...this.data, {...item, id: getId()}];
			}
			this.$refs.editor.close();
		},
		loadImage(item) {
			let label = item.label;
			if (this.preview) {
				if (this.fileMap && label in this.fileMap) {
					return this.fileMap[label];
				}
			}

			return this.placeholdImage;
		},
		formatLabel(item) {
			let label = item.label;
			if (this.preview) {
				while (/\${(.*?)}/.test(label)) {
					let before = label;
					// console.log(label);
					label = label.replaceAll(
						/\${(.*?)}/g,
						(_, key) => this.formatData[key] || ''
					);
					console.log(`${before} -> ${label}`);
				}
			}

			return label;
		},
		async doSwitchItem(item) {
			this.$message.info(`切换文字 ${item.id}`);
			this.filename = item.id;
			if (item.folder) {
				const data = await loadEntries(item.folder);
				// files list {file, name}
				// console.log(data);
				if (data && data.length) {
					this.fileMap = data.reduce((acc, curr) => {
						acc[curr.name] = curr.file;
						return acc;
					}, {});
				}
			}
			return Promise.resolve();
		},
	},
};
</script>

<style scoped>
.grid-line {
	background: linear-gradient(-90deg, rgba(0, 0, 0, 0.1) 1px, transparent 1px) repeat scroll 0%
			0% / 10px 10px,
		rgba(0, 0, 0, 0) linear-gradient(rgba(0, 0, 0, 0.1) 1px, transparent 1px) repeat scroll
			0% 0% / 10px 10px;
}
</style>
