<template>
	<div
		class="image overflow-hidden has-remove"
		:style="{width: `${width}px`, height: `${height}px`}"
		@dblclick="onEdit"
	>
		<span class="remove" @click="onRemove">
			<icon-x-circle />
		</span>
		<el-image class="" v-loading="loading" fit="cover" :src="image"></el-image>
	</div>
</template>

<script>
export default {
	name: 'YmeImage',
	props: {
		data: {
			type: Object,
			required: true,
		},
		file: {
			type: [File, String],
			default: '',
		},
	},
	data() {
		return {
			loading: false,
			// width: 100,
			// height: 100,
			image: 'data:image/png;base64,R0lGODlhAQABAAAAACH5BAEKAAEALAAAAAABAAEAAAICTAEAOw==',
		};
	},
	computed: {
		width() {
			return this.data.width || 100;
		},
		height() {
			return this.data.height || 100;
		},
	},
	watch: {
		file() {
			this.loadImage();
		},
	},
	methods: {
		onRemove() {
			this.$emit('remove', this.data);
		},
		onEdit() {
			this.$emit('dblclick');
		},
		loadImage() {
			console.log('load image', this.file);
			if (!this.file) return;
			if (typeof this.file === 'string' && this.file.startsWith('data:image')) {
				this.image = this.file;
				return;
			}

			this.loading = true;
			const reader = new FileReader(this.file);
			reader.onload = (e) => {
				console.log('完成 ', this.file.name);
				this.loading = false;
				if (
					e.target.result.toString() !==
					'data:image/png;base64,R0lGODlhAQABAAAAACH5BAEKAAEALAAAAAABAAEAAAICTAEAOw=='
				) {
					this.image = e.target.result;
					// this.$nextTick(() => {
					// 	this.$emit('done', {name});
					// });
				}
			};
			reader.readAsDataURL(this.file);
		},
	},
	mounted() {
		this.loadImage();
	},
};
</script>
