import {exportPreview, wait} from '../utils';

export default {
	data() {
		return {
			exporting: false,
			exportTotal: 0,
			exportCurrent: 0,
			filename: 'mixed',
			showList: false,
			listData: [],
			//
			failList: [],
			currentIndex: null,
			currentItem: null,
		};
	},
	computed: {
		exportFilename() {
			return this.filename || 'mixed';
		},
	},
	watch: {
		currentIndex(val) {
			if (val >= 0 && val < this.listData.length) {
				this.currentItem = this.listData[val];
			} else {
				this.currentItem = null;
			}
		},
		currentItem(item) {
			this.doSwitchItem(item);
		},
	},
	methods: {
		onDownloadCommand(command) {
			if (command === 'download-all') {
				this.exportAllImages();
			}
		},
		cleanList() {
			this.currentIndex = null;
			this.listData = [];
			this.failList = [];
		},
		switchListData(data = []) {
			this.currentIndex = null;
			//
			// if (data.length > 0) {
			// 	if (!data[0]._id) {
			// 		for (const item of data) {
			// 			item._id = 'x-' + getId();
			// 		}
			// 	}
			// }
			this.listData = [...data];
			this.$nextTick(() => {
				this.$message.warning(`重新设置数据，总共 ${data.length} 件`);
				this.currentIndex = 0;
			});
		},
		onListChange(index) {
			this.currentIndex = index;
		},
		// 做切换数据
		async doSwitchItem() {
			return true;
		},
		// 切换完成
		canExport() {
			return true;
		},
		async exportImage() {
			const filename = this.exportFilename;
			if (this.canExport()) {
				const $loading = this.$loading({
					lock: true,
					text: '下载 ' + filename,
				});

				try {
					await exportPreview(filename + '.png');
				} catch (error) {
					//-
				} finally {
					$loading.close();
				}
			} else {
				this.$message.error('没图片');
			}
		},
		async exportAllImages(list = null) {
			const jobs = list ?? [...this.listData];

			if (jobs.length === 0) {
				this.$message.error('没数据');
				return;
			}

			const $loading = this.$loading({
				lock: true,
				text: '',
			});

			const $notify = this.$notify({
				title: '开始导出图片...',
				type: 'warning',
				message: '关闭窗口可以取消导出',
				duration: 0,
				onClose: () => {
					this.exporting = false;
				},
			});

			this.exporting = true;
			this.$nextTick(async () => {
				const fail = [];

				for (const job of jobs) {
					if (!this.exporting) break;
					try {
						// switch item
						await this.doSwitchItem(job);
						// wait 5s
						$loading.text = '等待 5 秒后下载 ' + this.exportFilename;
						await wait(5000);
						await exportPreview(this.exportFilename + '.png');
						$loading.text = '下载成功 ' + this.exportFilename + ' , 2 秒后下一个';
						await wait(2000);
					} catch (error) {
						console.error(error);
						fail.push(job);
					} finally {
						//
					}
				}

				this.exporting = false;
				$notify.close();
				$loading.close();
				this.$message.success('导出完成');

				if (fail.length > 0) {
					this.$notify({
						title: '失败',
						message: '导出失败的有 ' + fail.length + ' 个',
					});
				}
			});
		},
	},
};
